

//module下的子文件
const studyModule={
  //命名空间，一定要开启
  namespaced:true,
  state:{
    msg:100,
    ftt:'ftt'
  },
  // 如计算函数
  getters:{
    msg2:function(state){
      return state.msg*10
    }
  },
  //mutations：直接同步改变state，
  // 在组件中使用 this.$store.commit提交来触发mutations,修改成功会被devtools记录下来
  mutations:{
    update(state,payload ){
      state.msg += payload.count
    }
  },
  // actions:间接修改state里面的数据，里面方法都是异步的比如调接口、定时器等，
  // 也可以直接修改state,但devTools不会记录不便于调试所以不建议使用
  // 在组件中使用this.$store.dispatch派发来触达actions方法
  // 可以在actions中，提交一个mutations方法来修改state
  actions:{
    aupdate(store,payload){
      setTimeout(()=>{
        store.commit('update',payload)
      },3000)
    }
  }
}
export default studyModule
