// 引入封装的fetch
import fetch from '@/utils/fetch'

const songModule = {
  // 命名空间
  namespaced: true,
  state: {
    sMsg: 2001,
    Music: []
  },
  // 计算函数
  getters: {},
  // 直接改变state
  mutations: {
    upList(state, payload) {
      state.Music = payload
    }
  },
  // 用于后端api交互
  actions: {
    getMusic(store,payload) {
      fetch({
        url: '/soso/fcgi-bin/client_search_cp',
        method: 'GET',
        params: payload,
      }).then(res=>{
        console.log(res)
        store.commit('upList', res.song.list)
      })
    }
  },
}
// 抛出
export default songModule