// 引入fetch
import fetch from './fetch'//axios实例（promise对象）
// ES6 模块：任何一个js文件都可以看成是一个独立作用域的模块

// 第一种写法
// export aaa
// export bbb
// import { aaa, bbb } from './xxx.js'
// 第二种写法：在同一个模块中，最多只能使用一次 export default
// export default
// import ccc from './xxx.js'


// 登入接口

export function login(data){
  return fetch({
    // 请求地址
    url:'/user/login',
    // 请求方式
    method:'POST',
    data
  })
}
// 注册接口
export function regist(data){
  return fetch({
    url:'/user/regist',
    method:'POST',
    data
  })
}
// 获取列表
export function getHotGoodList(params){
  return fetch({
    url:'/goods/acquires',
    method:'GET',
    params
  })
}
// 获取详情
export function getGoodDetail(params){
  return fetch({
    url:'/goods/goodDetail',
    method:'GET',
    params
  })
}
// 添加到购物车
export function addToCart(data){
  return fetch({
    url:'/carts/addCarts',
    method:'POST',
    data
  })
}
//获取购物车
export function getCartList(params){
  return fetch({
    url:'/carts/CartsList',
    method:'GET',
    params
  })
}
//更改购物车数量
export function updateCartsNum(data){
  return fetch({
    url:'/carts/updateCartsNum',
    method:'POST',
    data
  })
}
// 删除购物车
export function DeleteCartsGoods(params){
  return fetch ({
    url:'/carts/DeleteCartsGoods',
    params
  })
}
//统一抛出接口
export default{
  login,
  regist,
  getHotGoodList,
  getGoodDetail,
  addToCart,
  getCartList,
  updateCartsNum,
  DeleteCartsGoods
}