// 引入vue
import Vue from 'vue'
//引入vueRouter
import VueRouter from 'vue-router'
// 注册VueRouter
Vue.use(VueRouter)
//引入组件路径
// 同步写法
// import Fx from './pages/Fx.vue'
// import SouY from './pages/SouY.vue'
// import Me from './pages/Me.vue'

// 路由懒加载写法，节省性能
const Fx =()=>import('./pages/Fx.vue')
const SouY =()=>import('./pages/SouY.vue')
const Me =()=>import('./pages/Me.vue')
const Cart=()=>import('./pages/Cart.vue')
const Hot=()=>import('@/components/SouY/Hot.vue')
const New=()=>import('@/components/SouY/New.vue')
const Login=()=>import('./pages/Login.vue')
const Regist=()=>import('./pages/Regist.vue')
const Detail=()=>import('./pages/Detail.vue')

// @别名 代表就是的当前代码所在绝对路径 + /src
// import a from '@/components/a.vue'
// import b from '@/components/a.vue'
// import c from '@/pages/Me.vue'
// import Hot from '@components/SouY/Hot.vue'
// import New from '@components/SouY/New.vue'
// 创建router实例
let router = new VueRouter({
  // hash模式url带有#号（唯一缺点）
  // history模式没有#号，好看一点；缺点：部署后刷新视图会报404（解决方案需要后端做重定向）
  mode:"hash",
  // routes:[
  //   {path:'/SouY',component:SouY},
  //   {path:'/fx',component:Fx},
  //   {path:'/Me',component:Me}
  // ]
  //  命名视图：给router-view命名
  // routes:[
  //   {path:'/abc',pages:{
  //     aaa:a,
  //     bbb:b,
  //     ccc:c
  //   }},
  // ]
  routes:[
    // 别名：更加容易记忆的名字
    {path:'/SouY', alias:'/s',component:SouY},
    {path:'/fx',alias:'/f',component:Fx},
    {path:'/Me',alias:'/m',component:Me},
    {path:'/Cart',component:Cart,props:true},
    {path:'/Login',component:Login},
    {path:'/Regist',component:Regist},
    // 开启路由传参
    {path:'/Detail/:id',component:Detail,props:true},
    // 重定向：当访问某个路径时直接跳转到指定的类一个路径
    {path:'/',redirect:'/s'},
    // 二级路由
    {
      path:'/SouY',
      component:SouY,
      children:[
        {path:'hot',component:Hot},
        {path:'new',component:New},
      ]
    }
    
  ]
})
// 抛出实例
export default router