// 引入vue
import Vue from 'vue'
// 引入vuex
import Vuex from 'vuex'
// 注册vuex
Vue.use(Vuex)

// 引入子状态管理
import study from './modules/study'
import song from './modules/song'
import cart from './modules/cart'

// 创建实例
const store= new Vuex.Store({
  //这个是用于把子状态管理文件进行合并
  modules:{
    song,
    study,
    cart
  }
})
// 抛出
export default store