
import {getCartList} from '@/utils/api'

export default{
  namespaced:true,
  state:{
    list:[],
  },
  getters:{
    full:function(state){
      let arr=state.list.filter(ele=>ele.checked==false) //获取到checked=false 的复选框
      //console.log(arr)
      return arr.length==0 ?true:false  //当arr里面没有=false的复选框的时候full就等于true，反之等于false
    },
    jiage:function(state){
      var q=0;
      state.list.map(ele=>{
        if(ele.checked){
          q += ele.num*ele.good.price*100
        }
      })
      return q
    }
  },
  mutations:{
    upList(state,payload){
      payload.map((ele,index)=>{
        payload[index].checked=true //给list加上checked的变量
      }),
      state.list=payload
    },
    upListIt(state,payload){
      //console.log('payload',payload) //获取前端事件传过来的参数
      state.list[payload.index].checked=payload.check  //前端点击后传回新的checked状态，根据状态数据重新渲染
    },
    cheAll(state,payload){
      state.list.map((ele,idx)=>{
        state.list[idx].checked=!payload
      })
    }
  },
  actions:{
    getList(store){
      let params={}
      getCartList(params).then(res=>{
        store.commit('upList',res) //把列表数据给mutations
      })
    },
  }
}